import { previewImg } from '../../commons/js/common.js';
import {mapState} from 'vuex';
import {locationData} from '../../commons/js/list.js';
import HMmessages from "@/element/HM-messages/HM-messages.vue"
export default {
	name:'travels',
	components:{
		HMmessages
	},
	data() {
		return {
			num:0,
			address:'',
			titledata:'',
			tipsdata:'',
			topimg:[],
			videos:'',
			uploadvideos:false,
			fication:[
				{
					name:"景点"
				},
				{
					name:"美食"
				},
				{
					name:"网红打卡"
				}
			]
		}
	},
	onLoad() {
		uni.getSystemInfo({
		    success: function (res) {
				console.log(res.statusBarHeight)
		        console.log(res.model);
		        console.log(res.pixelRatio);
		        console.log(res.windowWidth);
		        console.log(res.windowHeight);
		        console.log(res.language);
		        console.log(res.version);
		        console.log(res.platform);
		    }
		});
	},
	created() {
		this.getLocation()
	},
	methods: {
		menubtn(index){
			this.num = index
		},
		// 图片预览
		preImg(index){
			// 预览图片
			previewImg(this.topimg,index)
		},
		// 上传图片
		uploadImg(){
			uni.showLoading({
				title:'上传中',
				icon:'../../static/loading.svg'
			})
			uni.chooseImage({
				count: 6, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: res => {
				 uni.hideLoading();
				 this.topimg = [...this.topimg,...res.tempFilePaths];
				}
			})
		},
		// 上传视频
		uploadVideo(){
			var self = this;
			uni.showLoading({
				title:'上传中',
				icon:'../../static/loading.svg'
			})
			uni.chooseVideo({
				count: 1,
				maxDuration:20,
				sourceType: ['camera', 'album'],
				success: function (res) {
					uni.hideLoading();
					self.uploadvideos = true;
					self.videos = res.tempFilePath;
				}
			});
		},
		// 删除图片
		deleteImg(index){
			this.topimg.splice(index,1);
		},
		// 删除视频
		deleteVideo(){
			this.videos = '';
			this.uploadvideos = false;
		},
		getLocation(){
			locationData()
			.then(res => {
				this.address = res.result.ad_info.city
			})
			.catch(err => {
				console.log(err)
				// this.addressVal = '无锡市'
			})
		},
		// 发布函数
		publishContext(){
			let data ={
				classifydata:this.fication[this.num].name,
				title:this.titledata,
				content:this.tipsdata,
				album:this.topimg,
				video:this.videos,
				address:this.address
			}
			console.log(data)
		}
	}
}
